import { Script, ScriptStrategy } from 'gatsby';
import React, { FC, useEffect } from 'react';

type Props = {};

const ExternalScriptsLoader: FC<Props> = () => {
  useEffect(() => {
    let isExternalScriptsInit = false;
    const loadExternalScripts = (event: Event) => {
      event.currentTarget?.removeEventListener(event.type, loadExternalScripts);
      if (isExternalScriptsInit) {
        return;
      }
      isExternalScriptsInit = true;
      //cookies-consent
      const cookiesConsentScript = document.createElement('script');
      cookiesConsentScript.type = 'text/javascript';
      cookiesConsentScript.async = true;
      cookiesConsentScript.onload = () => {
        // @ts-ignore
        window.cookieconsent?.run({
          notice_banner_type: 'simple',
          consent_type: 'express',
          palette: 'light',
          language: 'en',
          page_load_consent_levels: ['strictly-necessary'],
          notice_banner_reject_button_hide: false,
          preferences_center_close_button_hide: false,
          page_refresh_confirmation_buttons: false,
          website_name: 'Telgorithm.com',
          website_privacy_policy_url: '/policies/privacy-policy',
        });
      };
      cookiesConsentScript.src =
        'https://www.termsfeed.com/public/cookie-consent/4.0.0/cookie-consent.js';
      document.body.appendChild(cookiesConsentScript);

      //js-hs
      const jsHsScript = document.createElement('script');
      jsHsScript.type = 'text/javascript';
      jsHsScript.async = true;
      jsHsScript.src = '//js.hs-scripts.com/21630623.js';
      document.body.appendChild(jsHsScript);

      //linkedin
      const linkedinScript = document.createElement('script');
      linkedinScript.type = 'text/javascript';
      linkedinScript.async = true;
      linkedinScript.setAttribute('cookies-consent', 'tracking');
      linkedinScript.innerHTML = `
      _linkedin_partner_id = "4003508";
        window._linkedin_data_partner_ids = window._linkedin_data_partner_ids || [];
        window._linkedin_data_partner_ids.push(_linkedin_partner_id);
        (function(l) {
        if (!l){window.lintrk = function(a,b){window.lintrk.q.push([a,b])};
        window.lintrk.q=[]}
        var s = document.getElementsByTagName("script")[0];
        var b = document.createElement("script");
        b.type = "text/javascript";b.async = true;
        b.src = "https://snap.licdn.com/li.lms-analytics/insight.min.js";
        s.parentNode.insertBefore(b, s);})(window.lintrk);
    `;
      document.body.appendChild(linkedinScript);

      //salesoft
      const salesoftScript = document.createElement('script');
      salesoftScript.type = 'text/javascript';
      salesoftScript.async = true;
      salesoftScript.setAttribute('cookies-consent', 'tracking');
      salesoftScript.innerHTML = `
        (function(i,s,o,g,r,a,m){i['SLScoutObject']=r;i[r]=i[r]||function(){
          (i[r].q=i[r].q||[]).push(arguments)},i[r].l=1*new Date();a=s.createElement(o),
          m=s.getElementsByTagName(o)[0];a.async=1;a.src=g;m.parentNode.insertBefore(a,m)
          })(window,document,'script','https://scout-cdn.salesloft.com/sl.js','slscout');
          slscout(["init", "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ0IjoxMDk3MjJ9.pJ3N-hTFqi5Yiotfzlflh6J6B7hf_ePW5roDRrVKOdI"]);
    `;
      document.body.appendChild(salesoftScript);

      //zoominfo
      const zoominfoScript = document.createElement('script');
      zoominfoScript.type = 'text/javascript';
      zoominfoScript.async = true;
      zoominfoScript.src = 'https://ws.zoominfo.com/pixel/627aea554f6f68001256c330';
      zoominfoScript.setAttribute('cookies-consent', 'tracking');
      document.body.appendChild(zoominfoScript);
    };

    // delay loading by users action
    document.addEventListener('scroll', loadExternalScripts);
    document.addEventListener('mousemove', loadExternalScripts);
    document.addEventListener('touchstart', loadExternalScripts);
  }, []);

  return (
    <>
      <Script
        strategy={ScriptStrategy.idle}
        src={'https://www.googletagmanager.com/gtag/js?id=G-6S9SR1H9J6'}
        cookies-consent="tracking"
        onLoad={() => {
          //@ts-ignore
          window.dataLayer = window.dataLayer || [];
          function gtag() {
            //@ts-ignore
            dataLayer.push(arguments);
          }
          //@ts-ignore
          gtag('js', new Date());
          //@ts-ignore
          gtag('config', 'G-6S9SR1H9J6');
        }}
      ></Script>
    </>
  );
};

export default React.memo(ExternalScriptsLoader);
